<template>
  <div>
    <el-select
      v-model="state"
      filterable
      placeholder="请选择"
      @change="changeOption"
    >
      <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      >
      </el-option>
    </el-select>
    <el-table :data="list" stripe style="width: 100%">
      <el-table-column label="日期" width="180">
        <template slot-scope="scope">
          <el-link
            :href="
              '/topic/detail/' + scope.row.articleid + '#' + scope.row.replyid
            "
            target="_blank"
            >{{ scope.row.createtime }}</el-link
          >
        </template>
      </el-table-column>
      <el-table-column label="回報類型" width="180">
        <template slot-scope="scope">
          <el-link
            :href="
              '/topic/detail/' + scope.row.articleid + '#' + scope.row.replyid
            "
            target="_blank"
            >{{ scope.row.returntype }}</el-link
          ></template
        >
      </el-table-column>
      <el-table-column label="回報訊息">
        <template slot-scope="scope"
          ><el-link
            :href="
              '/topic/detail/' + scope.row.articleid + '#' + scope.row.replyid
            "
            target="_blank"
            >{{ scope.row.content }}</el-link
          ></template
        >
      </el-table-column>
      <el-table-column label="狀態" width="300">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.state"
            active-value="已處裡"
            inactive-value="未處裡"
            inactive-color="#ff4949"
            active-text="已處裡"
            inactive-text="未處裡"
            @change="changState(scope.row)"
          >
          </el-switch>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分頁 -->
    <p style="text-align: center">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[1, 2, 3, 10, 20, 30, 40]"
        :page-size="size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </p>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  data() {
    return {
      list: [],
      total: 0,
      currentPage: 1,
      size: 20,
      state: "ALL",
      options: [
        { value: "ALL", label: "ALL" },
        { value: "未處裡", label: "未處裡" },
        { value: "已處裡", label: "已處裡" },
      ],
    };
  },
  created() {
    $.ajax({
      url: "/topic/backstage/returnList?page=1&size=20&state=ALL",
      type: "get",
      async: false, //同步請求
      cache: false, //不快取頁面
      success: (response) => {
        this.list = response.list;
        this.total = response.total;
      },
      error: function (returndata) {
        console.log(returndata);
      },
    });
  },
  methods: {
    handleSizeChange(val) {
      this.size = val;
      $.ajax({
        url:
          "/topic/backstage/returnList?page=" +
          this.currentPage +
          "&size=" +
          this.size +
          "&state=" +
          this.state,
        type: "get",
        async: false, //同步請求
        cache: false, //不快取頁面
        success: (response) => {
          this.list = response.list;
          this.total = response.total;
        },
        error: function (returndata) {
          console.log(returndata);
        },
      });
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      $.ajax({
        url:
          "/topic/backstage/returnList?page=" +
          this.currentPage +
          "&size=" +
          this.size +
          "&state=" +
          this.state,
        type: "get",
        async: false, //同步請求
        cache: false, //不快取頁面
        success: (response) => {
          this.list = response.list;
          this.total = response.total;
        },
        error: function (returndata) {
          console.log(returndata);
        },
      });
    },
    changState(bean) {
      console.log(bean);
      $.ajax({
        url: "/topic/backstage/changeReturnState",
        type: "POST",
        async: false, //同步請求
        cache: false, //不快取頁面
        data: bean,
        success: (response) => {
          console.log(response);
        },
        error: function (returndata) {
          console.log(returndata);
        },
      });
    },
    changeOption() {
      $.ajax({
        url:
          "/topic/backstage/returnList?page=" +
          this.currentPage +
          "&size=" +
          this.size +
          "&state=" +
          this.state,
        type: "get",
        async: false, //同步請求
        cache: false, //不快取頁面
        success: (response) => {
          this.list = response.list;
          this.total = response.total;
        },
        error: function (returndata) {
          console.log(returndata);
        },
      });
    },
  },
};
</script>

<style>
.red {
  color: red;
}
</style>